import { ICache, IModuleContract, IRequestContext } from '@msdyn365-commerce/core';
import { InternalTelemetry } from '@msdyn365-commerce/telemetry-internal';
import { IUsedChunks } from '../_server/chunking';
import { PageContext } from '../store/page-context';

/*
 * Any changes to existing initialData properties should be accompanied
 * with JSON schema updates for their respective JSON string-ifiers in base-page-utils.ts
 *
 * For any new initialData properties, if the contents can be described well with a JSON schema create a new
 * Fast JSON Stringify constructor in base-page-utils.ts.
 * In addition make sure these properties are added to window.___initialData___ inside of base-page.tsx
 */
export interface IBasePageProps {
    initialData: PageContext;
    baseUrl?: string;
    requestUrl?: URL;
    chunks: IUsedChunks;
    css: string[];
    bodyContent: string;
    headContent: string;
    bodyBeginContent: string;
    bodyEndContent: string;
    isDebug: boolean;
    isEditor: boolean;
    context: IRequestContext;
    jsHashedUrls?: string[];
    themeCss: string[];
    appData: { bv: string };
    telemetry: InternalTelemetry;
    bodyClassName: string;
    experimentClientInitConfig?: string;
    prefetchLinks?: string;
}
